<template>
  <div :class="`languages-github ${this.mode}`">
    <div
      class="chart-language-github"
      height="400"
      v-if="this.series.length == 0"
    ></div>

    <apexchart
      id="chart"
      width="100%"
      height="400"
      type="bar"
      :options="chartOptions"
      :series="series"
      v-if="this.series.length > 0 && this.mode == 'dark'"
    ></apexchart>

    <apexchart
      id="chart"
      width="100%"
      height="400"
      type="bar"
      :options="chartOptions"
      :series="series"
      v-if="this.series.length > 0 && this.mode == 'light'"
    ></apexchart>

    <div class="chart-description">
      <h2 v-if="this.lang == 'pt'">Meus Projetos</h2>
      <h2 v-else>My Projects</h2>
      <p v-if="this.lang == 'pt'">
        Neste segmento, são exibidas as métricas reais de uso de diferentes
        linguagens/tecnologias obtidas através do meu perfil no GitHub.
      </p>
      <p v-else>
        In this section, you can view measurements from different programming
        languages/technologies usage, which it had been got through my profile
        on GitHub.
      </p>
      <p v-if="this.lang == 'pt'">
        Grande parte dos projetos computados foram desenvolvidos exclusivamente
        para aprendizado e apresentação de conhecimentos. Além desses possuo
        alguns outros projetos privados nos quais utilizei
        <span class="tecnologias">PHP</span>,
        <span class="tecnologias">Lumen</span>,
        <span class="tecnologias">JavaScript</span>,
        <span class="tecnologias">Electron</span>,
        <span class="tecnologias">Cordova</span>,
        <span class="tecnologias">Ionic</span>,
        <span class="tecnologias">MySQL</span>.
      </p>

      <p v-else>
        Most of the projects developed exclusively for learning and knowledge
        presentation. Besides these, I made some other private projects, in
        which, I coded with
        <span class="tecnologias">PHP</span>,
        <span class="tecnologias">Lumen</span>,
        <span class="tecnologias">JavaScript</span>,
        <span class="tecnologias">Electron</span>,
        <span class="tecnologias">Cordova</span>,
        <span class="tecnologias">Ionic</span>,
        <span class="tecnologias">MySQL</span>.
      </p>

      <p v-if="this.lang == 'pt'">
        Para mais detalhes, acesse o meu
        <a
          class="link"
          href="https://www.github.com/devmatheusguerra"
          target="_blank"
        >
          <i class="bx bxl-github"></i> perfil do GitHub.
        </a>
      </p>
      <p v-else>
        To see more details, visit my
        <a
          class="link"
          href="https://www.github.com/devmatheusguerra"
          target="_blank"
        >
          <i class="bx bxl-github"></i> GitHub profile.
        </a>
      </p>
    </div>
  </div>
</template>

<script>
import apexchart from "vue3-apexcharts";
import { getMetrics } from "@/functions/githubConsumer";
export default {
  name: "GithubLanguages",
  components: {
    apexchart,
  },
  props: ["lang", "mode"],
  data() {
    return {
      series: [
        {
          data: [],
        },
      ],
      chartOptions: {
        colors: [
          "#F2EABE",
          "#E9C7AB",
          "#E5B9AF",
          "#E5BACB",
          "#C6B0CD",
          "#A9AACD",
          "#9EA0CD",
          "#ACC6E1",
          "#B1D5DD",
          "#B1D4BF",
          "#D4E1BE",
        ],
        chart: {
          width: "100%",
          height: "380px",
          type: "bar",
        },
        plotOptions: {
          bar: {
            barHeight: "100%",
            distributed: true,
            horizontal: true,
            dataLabels: {
              position: "bottom",
            },
          },
        },
        title: {
          text: "Linguagens no meu GitHub",
          align: "center",
          floating: true,
          style: {
            color: "#9e9e9e",
          },
        },
        dataLabels: {
          enabled: true,
          textAnchor: "start",
          style: {
            colors: ["#fff"],
          },
          formatter: function (val, opt) {
            return opt.w.globals.labels[opt.dataPointIndex] + ":  " + val + "%";
          },
          offsetX: 0,
          dropShadow: {
            enabled: true,
          },
        },
        stroke: {
          width: 1,
          colors: ["#fff"],
        },
        yaxis: {
          labels: {
            show: false,
          },
        },
        xaxis: {
          categories: [],
        },
        legend: {
          show: true,
          position: "bottom",
          horizontalAlign: "center",
          fontSize: "14px",
          labels: {
            colors: this.mode == "dark"? "#9e9e9e": "#000",
          },
        },
        tooltip: {
          theme: "dark",
          x: {
            show: true,
          },
          y: {
            title: {
              formatter: function () {
                return "(%)";
              },
            },
          },
        },
      },
    };
  },
  mounted() {
    // this.chartOptions.title.style.color = this.mode == "dark" ? "#fff" : "#000";
    setInterval(() => {
      this.chartOptions.title.style.color = "#9e9e9e";
    }, 100);

    getMetrics().then((langs) => {
      // Sort by percentage
      let languages = [];
      for (var lang in langs) {
        languages.push([lang, langs[lang]]);
      }

      languages.sort(function (a, b) {
        return b[1] - a[1];
      });

      let langObj = {};
      for (var i = 0; i < languages.length; i++) {
        langObj[languages[i][0]] = languages[i][1];
      }

      langs = langObj;




      Object.keys(langs).forEach((key) => {
        this.chartOptions.xaxis.categories.push(key);
      });

      Object.values(langs).forEach((v) => {
        this.series[0].data.push(parseFloat(v.toFixed(1)));
      });
    });
  },
};
</script>

