<template>
  <div :class="`toggle ${this.mode}`" @click="this.changeMode(this)">
    <div class="selector"></div>
    <i class="bx bx-sun"></i>
    <i class="bx bx-moon"></i>
  </div>
</template>

<script>
export default {
  name: "ToggleMode",
  props: ["mode"],
  methods: {
    changeMode(elem) {
      elem.$el.classList.toggle("dark");
      if(elem.$el.classList.contains("dark")) {
        window.localStorage.setItem("mode", "dark");
      } else {
        window.localStorage.setItem("mode", "light");
      }
    },
  },
};
</script>

<style scoped>
.toggle {
  width: clamp(20px, 100%, 60px);
  aspect-ratio: 1/0.5;
  background-color: #fff;
  border: solid 1px #e6e6e6;
  border-radius: 100px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s ease-in;
  position: relative;
}


.selector {
  width: 40%;
  height: 90%;
  border-radius: 100%;
  position: absolute;
  left: 50%;
  top: 5%;
  background-color: inherit;
  transition: left 0.3s ease-in-out;
  color: inherit;
}

.dark {
  background-color: #2a2a2a;
  color: #fff;
}

.dark .selector {
  left: 10%;
  top: 5%;
}
</style>
