<template>
  <main :class="`${this.mode}`" v-if="this.language.length > 0 && this.mode.length > 0">
    <TopNavbar :mode="this.mode" :lang="this.language"></TopNavbar>
    <TopBanner :mode="this.mode" :lang="this.language"> </TopBanner>
    <GithubMetrics :mode="this.mode" :lang="this.language"></GithubMetrics>
    <DividerSection />
    <GithubLanguages :mode="this.mode" :lang="this.language"></GithubLanguages>
    <DividerSection id="skills" />
    <SkillLanguages :mode="this.mode" :lang="this.language"></SkillLanguages>
    <DividerSection id="experiences" />
    <EducationJobs :mode="this.mode" :lang="this.language"></EducationJobs>
    <DividerSection id="studies" />
    <FreeCourses :mode="this.mode" :lang="this.language"></FreeCourses>
    <DividerSection id="projects" />
    <ProjectsPortfolio :mode="this.mode" :lang="this.language"></ProjectsPortfolio>
    <DividerSection id="contact" />
    <ContactSecion :mode="this.mode" :lang="this.language"></ContactSecion>
    <FooterSection :mode="this.mode"></FooterSection>
  </main>
</template>

<script>
import TopNavbar from "./TopNavbar.vue";
import TopBanner from "./TopBanner.vue";
import GithubMetrics from "./GithubMetrics.vue";
import GithubLanguages from "./GithubLanguages.vue";
import SkillLanguages from "./SkillLanguages.vue";
import EducationJobs from "./EducationJobs.vue";
import FreeCourses from "./FreeCourses.vue";
import DividerSection from "./DividerSection.vue";
import ContactSecion from "./ContactSecion.vue";
import FooterSection from "./FooterSection.vue";
import ProjectsPortfolio from "./ProjectsPortfolio.vue";

export default {
  name: "MainContent",
  components: {
    TopBanner,
    TopNavbar,
    GithubMetrics,
    GithubLanguages,
    SkillLanguages,
    EducationJobs,
    FreeCourses,
    DividerSection,
    ContactSecion,
    FooterSection,
    ProjectsPortfolio
},
  data() {
    return {
      language: "",
      mode: "",
    };
  },
  mounted() {
    setTimeout(() => {
      if (localStorage.getItem("language") == null) {
        localStorage.setItem("language", "pt");
        this.language = "pt";
      } else {
        this.language = localStorage.getItem("language");
      }

      if (localStorage.getItem("mode") == null) {
        localStorage.setItem("mode", "dark");
        this.mode = "dark";
      } else {
        this.mode = localStorage.getItem("mode");
      }

    }, 1000);

    setInterval(() => {
      this.language = localStorage.getItem("language");
      this.mode = localStorage.getItem("mode");
    }, 10);

    window.addEventListener("wheel", (e) => {
      if (e.deltaY < 0) {
        e.preventDefault();
      }
    });
  },
};
</script>

