<template>
  <div :class="`courses-section ${this.mode}`">
    <h2 v-if="this.lang == 'pt'">Estudos</h2>
    <h2 v-else>Studies</h2>
    <div class="certificates">
      <AluraCertificate
        class="certificate"
        v-for="certificate in certificates.slice(0, this.show)"
        :front="certificate.front"
        :back="certificate.back"
        :url="certificate.url"
        v-bind:key="certificate.url"
      ></AluraCertificate>
    </div>
    <div class="more-certificates" v-if="certificates.length > this.show">
      <button :class="`show-btn ${this.mode}`" v-if="this.lang == 'pt'" @click="showMore()">
        <i class="bx bx-plus"></i> Ver mais
      </button>
      <button :class="`show-btn ${this.mode}`" @click="showMore()" v-else>
        <i class="bx bx-plus"></i> See more
      </button>
    </div>
  </div>
</template>

<script>
import AluraCertificate from "./AluraCertificate.vue";
import certificates from "@/data/certificates";
export default {
  name: "FreeCourses",
  components: { AluraCertificate },
  props: ["lang","mode"],
  methods: {
    showMore: function () {
      this.show += 3;
      setTimeout(() => {
        var scrollPosition = document.body.scrollTop;
        // Scroll smooth animation option
        window.scrollBy({
          top: scrollPosition + 250,
          left: 0,
          behavior: "smooth",
        });
      }, 500);
    },
  },
  data() {
    return {
      show: 3,
      certificates
    };
  },
};
</script>

