<template>
  <div :class="`projects-section ${this.mode}`">
    <h2 v-if="this.lang == 'pt'">Projetos</h2>
    <h2 v-else>Projects</h2>
    <div class="row">
      <div 
      v-for="project in projects"
      v-bind:key="project.url"
      class="col">
        <video muted loop class="project">
          <source :src="project.video" type="video/webm">
        </video>
        <a class="download" target="_blank" :href="project.url" v-if="this.lang == 'pt'">
          <i class="bx bx-download"></i>
          Baixar Projeto
        </a>
        <a class="download" target="_blank" :href="project.url" v-else>
          <i class="bx bx-download"></i>
          Download Project
        </a>
      </div>
    </div>
    
     
  </div>
</template>

<script>
import projects from "@/data/projects";
export default {
  name: "ProjectsPortfolio",
  components: {},
  props: ["lang", "mode"],
  mounted(){
    document.querySelectorAll('.project').forEach(video => {
      video.onmouseout = () => {
        video.pause();
      }

      video.onmouseleave = () => {
        video.pause();
      }

      video.onmouseover = () => {
        video.play()
      }

      video.onmouseenter = () => {
        video.play()
      }

    })
  },
  data(){
    return {
      projects
    }
  }
};
</script>

