async function getUser () {
  const response_backup = await fetch(
    `https://devmatheusguerra.com.br/devmatheusguerra.github.io/api.php?r=user`
  )
  const data = await response_backup.json()
  return data
}

async function getMetrics () {
  const response = await fetch(
    `https://devmatheusguerra.com.br/devmatheusguerra.github.io/api.php?r=data`
  )
  const data = await response.json()
  return data
}

async function getTotalViewers(){
  const response = await fetch(
    `https://devmatheusguerra.com.br/devmatheusguerra.github.io/counter-views.php`
  )
  const total = await response.text()
  return total
}

const Github = {
  getMetrics,
  getUser,
  getTotalViewers
}

module.exports = Github
